@import url(https://rsms.me/inter/inter.css);
.li-sidebar-close {
  display: none;
}

.sidebar-close {
  color: #757575;
  border: none;
  font-size: 20px;
  font-weight: 600;
  text-align: left;
}

a {
  -webkit-transition: background 0.2s, color 0.2s;
  transition: background 0.2s, color 0.2s;
  color: inherit;
}

a:hover,
a:focus {
  text-decoration: none;
  color: inherit;
}

#sidebar-wrapper {
  z-index: 1000;
  position: fixed;
  left: 180px;
  width: 0;
  height: 100%;
  margin-left: -180px;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #383838;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  box-shadow: 0px 0px 3px 4px #1f1f1f;
}

#wrapper.toggled #sidebar-wrapper {
  width: 180px;
}

.my-name {
  color: #757575;
  font-size: 30px;
  font-weight: 900;
  padding: 0px 0px 20px 44px;
  -webkit-transition: background-color .7s;
  transition: background-color .7s;
}

.name-cap {
  padding-right: 0px;
  font-size: 36px;
}

.first-name, .last-name {
  height: 40px;
}

#home {
  color: #757575;
  font-size: 30px;

  height: 100px;
  font-weight: 900;
  padding: 20px 20px 20px 16px;
}

.sidebar-nav {
  position: absolute;
  top: 105px;
  width: 180px;
  margin: 40px 0 0 0;
  padding: 0;
  list-style: none;
  display: block;
}

.sidebar-nav > li {
  text-indent: 10px;
  line-height: 42px; 
}

.sidebar-nav > li a {
  display: block;
  text-decoration: none;
  color: #757575;
  font-weight: 600;
  font-size: 20px;
  -webkit-transition: background-color .7s;
  transition: background-color .7s;
}

.sidebar-nav > li > a:hover,
li.active-nav a 
 {
  text-decoration: none;
  color: #fff;
  background: #757575;
  border-left: 10px solid #0c7664;
  text-indent: 0px;

}

.sidebar-nav > li > a:hover > i,
li.active-nav i
{
  text-indent: 10px;
}


.my-name:hover {
  color: #fff;
  background: #757575;
  border-left: 10px solid #0c7664;
  padding-left: 34px;
}

.sidebar-nav > li > a i.fa {
  font-size: 20px;
  width: 50px;
}

#sidebar-toggle,
#header {
  display: none;
}

@media (min-width: 992px) {
  .li-sidebar-close {
    visibility: hidden;
  }

  #sidebar-wrapper {
    width: 180px;
  }

  #wrapper.toggled #sidebar-wrapper {
    width: 60px;
  }

}

@media (min-width: 768px) and (max-width: 991px) {
  #sidebar-wrapper {
    width: 60px;
  }

  .my-name {
    padding-left: 16px;
  }

  .my-name:hover {
    padding-left: 6px;
  }

  .name-cap {
    padding-right: 16px;
  }

  .name-back {
    display: none;
  }


  .last-name {
    padding-left: 3px;
  }

}

@media (max-width: 767px) {
  
  #wrapper.toggled .li-sidebar-close {
    display: block;
  } 

  #wrapper.toggled #sidebar-wrapper {
    width: 180px;
  }

}


.headline {
  padding: 8px 24px;
  color: #ffffff;
  background-color: #0c7664;
}


@media (max-width: 767px) {
  #about {
    font-size: 16px;
  }
}


#hero {
  padding: 4px;
  background-color: #000;
  position: relative;
  
}

@media (min-width: 768px) and (max-width: 991px) {
  #hero {
    padding: 0px;
  }
}

@media (max-width: 767px) {
  #hero {
    padding: 4px;
  }

  .hero-msg-1 {
    position: absolute;
    top: -40px;
    left: 0px;
    font-size: 10px;
  }

}
* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  padding-bottom: 30px;
  position: relative;
  min-height: 100%;
  background-image: url(/portfolio2/static/media/cork-wallet.c93975e3.png)
}

p {
  padding: 10px;
  margin-bottom: 0px;
}

#header {
  display: none;
}

.skill-img {
  width: 100px;
}

/* won't need this if keep button over <a> */
a {
  -webkit-transition: background 0.2s, color 0.2s;
  transition: background 0.2s, color 0.2s;
}

/* won't need this if keep button over <a> */
a:hover,
a:focus {
  text-decoration: none;
}

#sidebar-toggle,
#header {
  display: none;
}

#content-wrapper {
  width: 100%;
  position: absolute;
  padding: 0px;
  top: 0px;
}

#wrapper.toggled #content-wrapper {
  position: absolute;
  margin-right: -180px;
}

@media (min-width: 992px) {

  #sidebar-toggle,
  #header {
    display: none;
  }

  #wrapper.toggled #content-wrapper {
    position: absolute;
    margin-right: -190px;
  }

  #content-wrapper {
    position: relative;
    top: 0;
  }

  #wrapper.toggled #content-wrapper {
    position: relative;
    margin-right: 60px;
  }
}

@media (min-width: 768px) and (max-width: 991px) {

  #sidebar-toggle,
  #header {
    display: none;
  }

  #wrapper.toggled #content-wrapper {
    position: absolute;
    margin-right: -180px;
  }

  #content-wrapper {
    position: relative;
    top: 0;
  }

  #wrapper.toggled #content-wrapper {
    position: relative;
    margin-right: 180px;
  }
}

@media (max-width: 767px) {

  #auth-image {
    width: 200px;
  }

  .main-section {
    position: absolute;
    top: 60px;
  }

  #wrapper.toggled .main-section:hover {
    cursor: pointer;
  }

  :target::before {
    content: "";
    display: block;
    height: 60px;
    margin-top: -60px;
    background-color: #e0e0e0;
  }

  #header {
    height: 60px;
    width: 100%;
    display: block;
    padding-top: 0px;
    line-height: 1.3;
    z-index: 2;
    background-color: #383838;
    box-shadow: 0px 0px 3px 4px #1f1f1f;
    color: #fff;
  }

  #header-title {
    color: inherit;
    font-size: 26px;
    font-weight: 600;
    width: 100%;
    display: inline-block;
  }

  #header-sub-title {
    color: inherit;
    font-size: 12px;
    display: inline-block;
    font-weight: 300;
    width: 100%;
  }

  /* burger button */
  #sidebar-toggle {
    position: fixed;
    top: 6px;
    left: -12px;
    display: block;
    color: #757575;
    background-color: rgb(56,56,56);
    border: none;
    font-size: 28px;
    text-align: left;
    -webkit-transition: color .7s;
    transition: color .7s;
    
  }

  #wrapper.toggled #content-wrapper {
    margin-right: 180px;
  }

}



