.project-box {
  width: 100%;
  padding: 0px;
  margin: 0px 0px;
}

.image-box {
  width: 100%;
  height: 50%;
  text-align: center;
  overflow: hidden;
  border-bottom: 10px solid #0c7664;
  border-top-left-radius: .5rem;
  border-top-right-radius: .5rem;
}

.project-title {
  box-sizing: border-box;
  font-size: 26px;
  line-height: 2rem;
  height: 62px;
  font-weight: bold;
  font-style: italic;
  color: #0c7664;
  padding: 16px 0px 16px 0px;
  border-bottom: 1px solid #0c7664;
}

.project-desc-list {
  height: 180px;
  margin-left: 0px;
  padding-top: 16px;
  font-size: 16px;
}

ul.project-desc-list li {
  list-style-type: disc;
  list-style-position: outside;
  text-align: left;
  padding: 0px 12px 16px 4px;
}

.project-links {
  height: 60px;
  margin-top: 30px;
  border-top: 1px solid #0c7664;
}

.project-link1,
.project-link2 {
  display: inline-block;
  width:50%;
  height: 100%; 
  padding: 12px;
  -webkit-transition: background-color .5s, box-shadow .5s;
  transition: background-color .5s, box-shadow .5s;
  cursor: default;
}

.project-link1:hover:not(.no-link),
.project-link2:hover:not(.no-link) {
  background-image: url(/portfolio2/static/media/cork-wallet.c93975e3.png);
  box-shadow: 2px 4px 4px 2px  rgba(0,0,0,0.7); 
  cursor: pointer;
}

.project-link1:hover:not(.no-link) {
  border-bottom-left-radius: .5rem;
}

.project-link2:hover:not(.no-link) {
  border-bottom-right-radius: .5rem;
}



div.project-box i {
  font-size: 30px;
  color: #0c7664;
  padding-right: 8px;
  display: inline-block;
  vertical-align: middle;
}

.project-link-desc{
  vertical-align: middle;
  font-size: 12px;
}


@media (max-width: 767px) {
  .project-box {
    width: 100%;
  }

